  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = this.data
    const wordCloud = new WordCloud('container', {
      data,
      wordField: 'x',
      weightField: 'value',
      color: '#122c6a',
      wordStyle: {
        fontFamily: 'Verdana',
        fontSize: [24, 80]
      },
      // 设置交互类型
      interactions: [{ type: 'element-active' }],
      state: {
        active: {
          // 这里可以设置 active 时的样式
          style: {
            lineWidth: 3
          }
        }
      }
    })

    wordCloud.render()

    this.getone()
    this.getTwo()
  },
  methods: {
    getone() {
      const data = this.data1
      const wordCloud = new WordCloud('containerG2', {
        data,
        wordField: 'name',
        weightField: 'value',
        colorField: 'name',
        imageMask: 'https://gw.alipayobjects.com/mdn/rms_2274c3/afts/img/A*07tdTIOmvlYAAAAAAAAAAABkARQnAQ',
        wordStyle: {
          fontFamily: 'Verdana',
          fontSize: [8, 32]
        }
      })

      wordCloud.render()
    },
    getTwo() {
      const data = this.data2
      const wordCloud = new WordCloud('containerGz', {
        data,
        wordField: 'name',
        weightField: 'value',
        colorField: 'name',
        wordStyle: {
          fontFamily: 'Verdana',
          fontSize: [8, 32],
          rotation: 0
        },
        // 返回值设置成一个 [0, 1) 区间内的值，
        // 可以让每次渲染的位置相同（前提是每次的宽高一致）。
        random: () => 0.5
      })

      wordCloud.render()
    }
  },
  created() {
    // var wrapperElement = '<div style=position:relative;></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
